import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { Pagination, TableBody, TableContainer, TableHead } from 'shared/components/Elements';
import {
	ActionsButtons,
	ConfirmModal,
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { useAppState } from 'shared/state';
import { useNewsTemplates } from '../../hooks';
import { usePagination, useRouter } from 'shared/hooks';
import { useDebounce } from 'use-debounce';
import { NewsTemplateType } from 'shared/typings/news.types';
import { useNewsContext } from '../../context/NewsContext';

export function Templates() {
	const { companyId } = useAppState();
	const { page, handlePage } = usePagination();
	const { searchQuery: query } = useNewsContext();
	const { match, history } = useRouter();

	const {
		deleteLoading,
		deleteTemplateHandler,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible
	} = useNewsTemplates();

	const [searchQuery] = useDebounce<string>(query, 500);

	return (
		<div>
			{companyId ? (
				<EntityContainer.All
					entity="templates"
					name="AllTemplates"
					url={`/${companyId}/notification-templates/`}
					params={{
						limit: 10,
						extra: {
							search: searchQuery
						},
						page
					}}
				>
					{({ items, isFetched, meta }) => {
						const templates = items as NewsTemplateType[];
						return (
							<div>
								<TableContainer classes={{ root: 'relative ' }}>
									<StyledTable
										aria-label="simple table"
										classes={{ root: 'table-fixed dark-mode-styled-table' }}
									>
										<TableHead>
											<StyledTableRow>
												<StyledTableHeadCell style={{ width: '30%' }}>Template</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>
													Created Date
												</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '30%' }}>Author</StyledTableHeadCell>
												<StyledTableHeadCell style={{ width: '20%' }}>Actions</StyledTableHeadCell>
											</StyledTableRow>
										</TableHead>
										<TableBody>
											{templates.map((item) => {
												const { id, title, created_by, created_date } = item;
												return (
													<StyledTableRow key={id}>
														<StyledTableCell>
															<Link to={`${match.path}/view/${item.id}`}>
																<span className="text-primary transition hover:text-primary-dark">
																	{title}
																</span>
															</Link>
														</StyledTableCell>
														<StyledTableCell>
															{dayjs(created_date).format('MMMM D YYYY, h:mm a')}
														</StyledTableCell>
														<StyledTableCell>{`${created_by.first_name} ${created_by.last_name}`}</StyledTableCell>
														<StyledTableCell>
															<div className="flex-center-x">
																<ActionsButtons.DeleteButton
																	clickHandler={() => showDeleteVisible(item)}
																/>
																<ActionsButtons.ViewButton
																	clickHandler={() => history.push(`${match.path}/view/${item.id}`)}
																/>
																<ActionsButtons.EditButton
																	clickHandler={() => history.push(`${match.path}/edit/${item.id}`)}
																/>
															</div>
														</StyledTableCell>
													</StyledTableRow>
												);
											})}
										</TableBody>
									</StyledTable>
									{!isFetched && <Loader tiled withOverlay />}
								</TableContainer>
								<ConfirmModal
									featureHandler={deleteTemplateHandler()}
									isOpen={Boolean(deleteVisible)}
									toggleHandler={closeDeletePopup}
									title={`Are you sure to delete the template ${deleteVisible?.title}?`}
									hasLoader
									isLoading={deleteLoading}
								/>
								<div className="news__pagination my-4">
									{items.length ? (
										<Pagination
											count={meta?.last_page}
											page={page ? +page : 1}
											defaultPage={6}
											siblingCount={1}
											color={'primary'}
											onChange={handlePage}
										/>
									) : null}
								</div>
							</div>
						);
					}}
				</EntityContainer.All>
			) : (
				<div className="flex justify-center">
					<Loader />
				</div>
			)}
		</div>
	);
}
